<template>
  <div class="leftoutbox">
    <div class="leftoutbox_title">
      <div class="fnlbbox">方案列表</div>
      <div class="adddeletoutbox">
        <div class="addbox">
          <img src="../../../../assets/img/add.png" alt />
          添加
        </div>
        <div class="deletbox">
          <img src="../../../../assets/delete.png" alt />
          删除
        </div>
      </div>
    </div>
    <div class="leftoutbox_contentoutbox">
      <vue-scroll>
        <div class="leftoutbox_contentoutbox_inbox">
          <div v-for="(item,index) in 13" :key="index" class="preitembox"></div>
        </div>
      </vue-scroll>
    </div>
  </div>
</template>
<script>
export default {
  name: "index"
};
</script>
<style lang="scss" scoped>
@import "@/styles/global.scss";
.leftoutbox {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: vw(284);
  height: vh(1039);
  .leftoutbox_title {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    width: vw(282);
    height: vh(40);
    border-left: 4px #70991f solid;
    border-bottom: 1px #ccc solid;
    .fnlbbox {
      color: #6e7b8b;
      font: bold vh(14) "微软雅黑";
      margin-left: vw(20);
    }
    .adddeletoutbox {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-start;
      align-items: center;
      margin-right: vw(20);
      .addbox {
        display: flex;
        flex-flow: row nowrap;
        justify-content: flex-start;
        align-items: center;
        color: #acb3ba;
        font: normal vh(12) "微软雅黑";
        margin-right: vw(10);
        cursor: pointer;
        img {
          width: vw(14);
          height: auto;
          margin-right: vw(3);
        }
      }
      .deletbox {
        display: flex;
        flex-flow: row nowrap;
        justify-content: flex-start;
        align-items: center;
        color: #acb3ba;
        font: normal vh(12) "微软雅黑";
        cursor: pointer;
        img {
          width: vw(12);
          height: auto;
          margin-right: vw(3);
        }
      }
    }
  }
  .leftoutbox_contentoutbox {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: vw(284);
    height: vh(999);
    overflow-x: hidden;
    overflow-y: auto;
    .leftoutbox_contentoutbox_inbox {
      display: flex;
      flex-flow: column;
      justify-content: flex-start;
      align-items: flex-start;
      width: vw(284);
      .preitembox {
        display: flex;
        flex-flow: column;
        justify-content: flex-start;
        align-items: flex-start;
        width: vw(284);
        height: vh(100);
        border-bottom: 1px #ccc solid;
        &:last-child{
           border-bottom:none;
        }
      }
    }
  }
}
</style>



