<template>
  <div class="leftcontentoutbox">
    <div class="leftcontentoutbox_leftoutbox" v-show="closeopenstatus">
      <leftoutbox />
    </div>
    <div class="leftcontentoutbox_rightoutbox">
      <rightoutbox />
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import leftoutbox from "./leftoutbox/index";
import rightoutbox from "./rightoutbox/index";
export default {
  name: "leftcontentoutbox",
  components: {
    leftoutbox,
    rightoutbox
  },
  computed: {
    ...mapState(["closeopenstatus"])
  }
};
</script>
<style lang="scss" scoped>
@import "@/styles/global.scss";
.leftcontentoutbox {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: flex-start;
 /*  width: vw(1138); */
  height: vh(1039);
  .leftcontentoutbox_leftoutbox {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: vw(284);
    height: vh(1039);
    border-right: 1px #ccc solid;
  }
  .leftcontentoutbox_rightoutbox {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: vw(853);
    height: vh(1039);
  }
}
</style>



