<template>
  <div class="rightoutbox">
    <div class="rightoutbox_title">
      <div class="imgbox">
        <img src="~@/assets/closeimg.png" alt @click="close" v-show="closeopenstatus" />
        <img src="~@/assets/openimg.png" alt @click="open" v-show="!closeopenstatus" />
      </div>
      <div class="titlebox">边界条件设置</div>
    </div>
    <div class="rightoutbox_contentoutbox"></div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "index",
  computed: {
    ...mapState(["closeopenstatus"])
  },
  methods: {
    close() {
      this.$store.commit("closeimg", false);
    },
    open() {
      this.$store.commit("openimg", true);
    }
  }
};
</script>
<style lang="scss" scoped>
@import "@/styles/global.scss";
.rightoutbox {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: vw(853);
  height: vh(1039);
  .rightoutbox_title {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    width: vw(851);
    height: vh(40);
    border-left: 4px #70991f solid;
    border-bottom: 1px #ccc solid;
    position: relative;
    .imgbox {
      display: flex;
      flex-flow: row nowrap;
      justify-content: flex-start;
      align-items: center;
      margin-left: vw(-5);
      position: absolute;
      cursor: pointer;
      img {
        /* width: vw(16); */
        height: vh(28);
      }
    }

    .titlebox {
      color: #6e7b8b;
      font: bold vh(14) "微软雅黑";
      margin-left: vw(20);
    }
  }
  .rightoutbox_contentoutbox {
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: vw(853);
    height: vh(999);
  }
}
</style>



